import Vue from 'vue'
import Router from 'vue-router'
import HelloWorld from '@/components/HelloWorld'
import home from '@/components/page/home'
import index from '@/components/page/index'
import video from '@/components/page/video'
import sub from '@/components/page/sub'
import user from '@/components/page/user'
import login from '@/components/page/login'
import regist from '@/components/page/regist'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      redirect:({'name':'home'})
    },
    {
      path: '/HelloWorld',
      name: 'HelloWorld',
      component: HelloWorld
    },
    {
      path: '/home',
      name: 'home',
      component: home,
      redirect:{'name':'index'}, 
      children:[
       {
        path:'/home/index',
        name:'index',
        component:index
       },
       {
        path:'/home/video',
        name:'video',
        component:video
       },
       ,
       {
        path:'/home/sub',
        name:'sub',
        component:sub
       } 
      ]
    },
    {
      path: '/user',
      name: 'user',
      component: user,

    },      
    {
      path:'/user/login',
      name:'login',
      component:login
    },
    {
      path:'/regist',
      name:'regist',
      component:regist
    }
  ]
})



