<template>
    <div>
        <div class="nav">
           <router-link to='/user'>返回</router-link>
           <router-link to='/regist' class="regist">注册</router-link>          
        </div>  

        <ul>
            <li><input type="text" @blur="de" v-model="x"></li>
            <li><input type="password"  @blur="de" v-model="y"></li>
            <div><button><router-link to='/home'>登陆</router-link></button></div>
        </ul>      
    </div>
</template>

<script>
export default {
    data(){
        return{
            x:'',
            y:''
        }
    },
    methods:{
        de(){
            if(this.x=''){
                alert('xx')
            }
        }
    }
}
</script>


<style lang='scss'>
    @import url('../../style/rest.css');
    $sz:25;
    .nav{
        height:43.98/$sz+rem;
        width: 100%;
        background: pink;
    }
    .regist{
        float: right;
    }
    li{
        border: 1px solid pink
    }
     button{
            display: block;
            margin: 100/$sz+rem auto;
            width: 323/$sz+rem;
            height:39/$sz+rem 
        }
</style>



